import * as React from "react";
import { RadialChart, Hint } from "react-vis";
import { colors } from "../../../design-system";

interface Props {}

const Income: React.FC<Props> = () => {
  const [value, setValue] = React.useState(0);

  const data = [
    { label: "Female", angle: 62.5 },
    { label: "Male", angle: 37.4 },
    { label: "Other", angle: 0.1 },
  ];

  console.log(value);
  return (
    <div>
      <p>Income Metrics</p>
      <RadialChart
        colorRange={[colors.greyDark, colors.blueDark, colors.blue]}
        animation
        data={data}
        width={300}
        height={300}
        showLabels
        onValueMouseOver={v => setValue(v)}
        onSeriesMouseOut={v => setValue(0)}
      />
      {value && <Hint value={value}>{value}</Hint>}
    </div>
  );
};

export default Income;
